.qualifying-round .logo-wrapper {
    padding: 5px 15px;
    background-color: var(--alt-bg);
    color: white;
}

li.null {
    outline: 5px solid transparent;
    cursor: pointer;
}

li.true {
    outline: 5px solid var(--eligible);
    border: 2px solid transparent !important;
}

li.false {
    outline: 5px solid transparent;
    border: 4px solid transparent !important;
    filter: contrast(60%);
    cursor: pointer;
}
